export const getTemplate = (type: string): Template => {
  return templates[type] || templates.premio
}

export const getTemplateTypes = (): string[] => {
  return Object.keys(templates)
}

export const getTemplateIcon = (type: string): string => {
  return templates[type]?.icon || "üéØ"
}

export const getTemplateBgColor = (type: string): string => {
  return templates[type]?.bgColor || "from-gray-900 to-black"
}

export const getTemplateUrgencyColor = (level: Template["urgencyLevel"]): string => {
  switch (level) {
    case "high":
      return "text-red-500"
    case "medium":
      return "text-yellow-500"
    case "low":
      return "text-green-500"
    default:
      return "text-gray-500"
  }
}
