export interface GeolocationData {
  latitude: number
  longitude: number
  accuracy: number
  altitude: number | null
  speed: number | null
  heading: number | null
  timestamp: number
}

export interface GeolocationOptions {
  enableHighAccuracy?: boolean
  timeout?: number
  maximumAge?: number
  watchPosition?: boolean
}

export interface LocationError {
  code: number
  message: string
  type: 'permission' | 'position' | 'timeout' | 'unsupported' | 'unknown'
}

export interface DeviceInfo {
  userAgent: string
  platform: string
  language: string
  screenResolution: string
  timezone: string
  fingerprint: string
}

export interface IPInfo {
  ip: string
  country?: string
  city?: string
  latitude?: number
  longitude?: number
}

export class GeoService {
  private static watchId: number | null = null

  static async getCurrentPosition(options: GeolocationOptions = {}): Promise<GeolocationData> {
    if (!navigator.geolocation) {
      throw this.createError({
        code: 0,
        message: "Geolocalizaci√≥n no soportada en este navegador",
        type: 'unsupported'
      })
    }

    const defaultOptions: PositionOptions = {
      enableHighAccuracy: true,
      timeout: 10000,
      maximumAge: 0,
      ...options
    }

    try {
      const position = await new Promise<GeolocationPosition>((resolve, reject) => {
        navigator.geolocation.getCurrentPosition(resolve, reject, defaultOptions)
      })

      return this.formatPosition(position)
    } catch (error: any) {
      throw this.handleGeolocationError(error)
    }
  }

  static watchPosition(
    onSuccess: (position: GeolocationData) => void,
    onError: (error: LocationError) => void,
    options: GeolocationOptions = {}
  ): () => void {
    if (!navigator.geolocation) {
      onError(this.createError({
        code: 0,
        message: "Geolocalizaci√≥n no soportada",
        type: 'unsupported'
      }))
      return () => {}
    }

    const defaultOptions: PositionOptions = {
      enableHighAccuracy: true,
      timeout: 10000,
      maximumAge: 0,
      ...options
    }

    this.watchId = navigator.geolocation.watchPosition(
      (position) => {
        try {
          const formattedPosition = this.formatPosition(position)
          onSuccess(formattedPosition)
        } catch (error: any) {
          onError(this.handleGeolocationError(error))
        }
      },
      (error) => {
        onError(this.handleGeolocationError(error))
      },
      defaultOptions
    )

    return () => {
      if (this.watchId !== null) {
        navigator.geolocation.clearWatch(this.watchId)
        this.watchId = null
      }
    }
  }

  static getDeviceInfo(): DeviceInfo {
    return {
      userAgent: navigator.userAgent,
      platform: navigator.platform,
      language: navigator.language,
      screenResolution: `${window.screen.width}x${window.screen.height}`,
      timezone: Intl.DateTimeFormat().resolvedOptions().timeZone,
      fingerprint: this.generateFingerprint()
    }
  }

  static async getIPInfo(): Promise<IPInfo> {
    try {
      const response = await fetch('https://ipapi.co/json/')
      if (!response.ok) throw new Error('Error al obtener informaci√≥n IP')
      
      const data = await response.json()
      return {
        ip: data.ip,
        country: data.country_name,
        city: data.city,
        latitude: data.latitude,
        longitude: data.longitude
      }
    } catch (error) {
      console.error('Error obteniendo informaci√≥n IP:', error)
      return { ip: 'desconocida' }
    }
  }

  static async getLocationDetails(latitude: number, longitude: number): Promise<{
    country: string
    city: string
    address: string
  }> {
    try {
      const response = await fetch(
        `https://nominatim.openstreetmap.org/reverse?format=json&lat=${latitude}&lon=${longitude}&zoom=18&addressdetails=1`
      )
      
      if (!response.ok) {
        throw new Error('Error al obtener detalles de ubicaci√≥n')
      }

      const data = await response.json()
      
      return {
        country: data.address.country || 'Desconocido',
        city: data.address.city || data.address.town || data.address.village || 'Desconocido',
        address: data.display_name || 'Direcci√≥n no disponible'
      }
    } catch (error) {
      console.error('Error obteniendo detalles de ubicaci√≥n:', error)
      return {
        country: 'Desconocido',
        city: 'Desconocido',
        address: 'Direcci√≥n no disponible'
      }
    }
  }

  private static formatPosition(position: GeolocationPosition): GeolocationData {
    return {
      latitude: position.coords.latitude,
      longitude: position.coords.longitude,
      accuracy: position.coords.accuracy,
      altitude: position.coords.altitude,
      speed: position.coords.speed,
      heading: position.coords.heading,
      timestamp: position.timestamp
    }
  }

  private static handleGeolocationError(error: GeolocationPositionError): LocationError {
    let type: LocationError['type'] = 'unknown'
    let message = 'Error desconocido al obtener ubicaci√≥n'

    switch (error.code) {
      case 1:
        type = 'permission'
        message = 'Acceso a la ubicaci√≥n denegado. Por favor, permite el acceso a tu ubicaci√≥n en la configuraci√≥n del navegador.'
        break
      case 2:
        type = 'position'
        message = 'No se pudo determinar tu ubicaci√≥n. Verifica tu conexi√≥n GPS y a internet.'
        break
      case 3:
        type = 'timeout'
        message = 'Tiempo de espera agotado al intentar obtener la ubicaci√≥n.'
        break
    }

    return this.createError({ code: error.code, message, type })
  }

  private static createError({ code, message, type }: {
    code: number
    message: string
    type: LocationError['type']
  }): LocationError {
    return { code, message, type }
  }

  private static generateFingerprint(): string {
    const components = [
      navigator.userAgent,
      navigator.language,
      new Date().getTimezoneOffset(),
      navigator.hardwareConcurrency || 'unknown',
      window.screen.colorDepth,
      window.screen.pixelDepth,
      window.screen.width,
      window.screen.height,
      navigator.vendor,
      navigator.platform,
      "canvas:" + this.getCanvasFingerprint()
    ]

    return components.join('|')
  }

  private static getCanvasFingerprint(): string {
    try {
      const canvas = document.createElement('canvas')
      const ctx = canvas.getContext('2d')
      if (!ctx) return 'no-canvas'

      canvas.width = 200
      canvas.height = 50
      
      ctx.textBaseline = 'top'
      ctx.font = '14px Arial'
      ctx.fillStyle = '#f60'
      ctx.fillRect(125, 1, 62, 20)
      
      ctx.fillStyle = '#069'
      ctx.fillText('GeoTracker:2024', 2, 15)
      ctx.fillStyle = 'rgba(102, 204, 0, 0.7)'
      ctx.fillText('Location:Verified', 4, 17)

      return canvas.toDataURL()
    } catch (e) {
      return 'canvas-error'
    }
  }

  static getAccuracyDescription(accuracy: number): string {
    if (accuracy <= 10) return "Excelente (GPS)"
    if (accuracy <= 30) return "Buena"
    if (accuracy <= 100) return "Moderada"
    if (accuracy <= 500) return "Baja"
    return "Muy baja"
  }

  static getAccuracyColor(accuracy: number): string {
    if (accuracy <= 10) return "text-green-500"
    if (accuracy <= 30) return "text-blue-500"
    if (accuracy <= 100) return "text-yellow-500"
    if (accuracy <= 500) return "text-orange-500"
    return "text-red-500"
  }
}
