import { GeoService, type GeolocationData } from "./geo-service"

export interface TrackingLink {
  id: string
  shortCode: string
  templateType: string
  isActive: boolean
  clicks: number
  captures: number
  createdAt: string
  expiresAt: string
}

export interface LocationCapture {
  id: string
  linkId: string
  latitude: number
  longitude: number
  accuracy: number
  altitude: number | null
  speed: number | null
  heading: number | null
  city: string | null
  country: string | null
  capturedAt: string
  deviceInfo: {
    ipAddress: string
    userAgent: string
    deviceFingerprint: string
    browserInfo: string
    batteryLevel?: number
    networkType?: string
    platform: string
    screenResolution: string
  }
}

export class TrackingService {
  private static STORAGE_KEY = 'tracking_data'
  private static API_BASE = "/api"

  private static async getStoredData(): Promise<{
    links: TrackingLink[]
    captures: LocationCapture[]
  }> {
    try {
      const data = localStorage.getItem(this.STORAGE_KEY)
      if (data) {
        return JSON.parse(data)
      }
    } catch (error) {
      console.error("Error reading stored data:", error)
    }
    return { links: [], captures: [] }
  }

  private static async saveData(data: { links: TrackingLink[], captures: LocationCapture[] }) {
    try {
      localStorage.setItem(this.STORAGE_KEY, JSON.stringify(data))
    } catch (error) {
      console.error("Error saving data:", error)
    }
  }

  static async createTrackingLink(templateType: string): Promise<TrackingLink> {
    try {
      const response = await fetch(`${this.API_BASE}/tracking-links`, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ templateType })
      })

      if (!response.ok) {
        throw new Error("Error al crear el enlace de rastreo")
      }

      const newLink = await response.json()

      // Guardar en almacenamiento local
      const data = await this.getStoredData()
      data.links.push(newLink)
      await this.saveData(data)

      return newLink
    } catch (error) {
      console.error("Error creating tracking link:", error)
      throw error
    }
  }

  static async getTrackingLinks(): Promise<TrackingLink[]> {
    try {
      const data = await this.getStoredData()
      return data.links
    } catch (error) {
      console.error("Error fetching tracking links:", error)
      throw error
    }
  }

  static async getLocationCaptures(): Promise<LocationCapture[]> {
    try {
      const data = await this.getStoredData()
      return data.captures
    } catch (error) {
      console.error("Error fetching location captures:", error)
      throw error
    }
  }

  static async captureLocation(
    linkId: string,
    shortCode: string
  ): Promise<LocationCapture> {
    try {
      // Obtener datos de ubicaci√≥n completos
      const locationData = await GeoService.getFullLocationData()

      // Obtener informaci√≥n del dispositivo
      const deviceInfo = await this.getDeviceInfo()

      const capture: LocationCapture = {
        id: `capture-${Date.now()}-${Math.random().toString(36).substr(2, 9)}`,
        linkId,
        latitude: locationData.latitude,
        longitude: locationData.longitude,
        accuracy: locationData.accuracy,
        altitude: locationData.altitude,
        speed: locationData.speed,
        heading: locationData.heading,
        city: locationData.city || null,
        country: locationData.country || null,
        capturedAt: new Date().toISOString(),
        deviceInfo: {
          ipAddress: locationData.ip || deviceInfo.ipAddress,
          userAgent: deviceInfo.userAgent,
          deviceFingerprint: deviceInfo.deviceFingerprint,
          browserInfo: deviceInfo.browserInfo,
          batteryLevel: deviceInfo.batteryLevel,
          networkType: deviceInfo.networkType,
          platform: deviceInfo.platform,
          screenResolution: deviceInfo.screenResolution
        }
      }

      // Guardar en almacenamiento local
      const data = await this.getStoredData()
      data.captures.push(capture)
      await this.saveData(data)

      // Actualizar estad√≠sticas del enlace
      const linkIndex = data.links.findIndex(l => l.id === linkId)
      if (linkIndex !== -1) {
        data.links[linkIndex].captures++
        await this.saveData(data)
      }

      return capture
    } catch (error) {
      console.error("Error capturing location:", error)
      throw error
    }
  }

  private static async getDeviceInfo() {
    const info: any = {
      userAgent: navigator.userAgent,
      platform: navigator.platform,
      screenResolution: `${window.screen.width}x${window.screen.height}`,
      ipAddress: "0.0.0.0",
      browserInfo: this.getBrowserInfo(),
      deviceFingerprint: await this.generateDeviceFingerprint(),
      networkType: (navigator as any).connection?.type || "unknown"
    }

    // Obtener nivel de bater√≠a si est√° disponible
    try {
      const battery = await (navigator as any).getBattery?.()
      if (battery) {
        info.batteryLevel = battery.level * 100
      }
    } catch (e) {
      console.warn("Battery API not available")
    }

    return info
  }

  private static getBrowserInfo(): string {
    const ua = navigator.userAgent
    const browserRegex = /(chrome|safari|firefox|edge|opera|ie)\/([\d.]+)/i
    const match = ua.match(browserRegex)
    if (match) {
      return `${match[1].charAt(0).toUpperCase() + match[1].slice(1)} ${match[2]}`
    }
    return "Unknown Browser"
  }

  private static async generateDeviceFingerprint(): Promise<string> {
    const components = [
      navigator.userAgent,
      navigator.language,
      new Date().getTimezoneOffset(),
      screen.width,
      screen.height,
      screen.colorDepth,
      navigator.hardwareConcurrency,
      navigator.deviceMemory,
      navigator.platform,
      (navigator as any).connection?.type
    ]
    
    const fingerprint = components.join('|')
    const encoder = new TextEncoder()
    const data = encoder.encode(fingerprint)
    const hashBuffer = await crypto.subtle.digest('SHA-256', data)
    const hashArray = Array.from(new Uint8Array(hashBuffer))
    return hashArray.map(b => b.toString(16).padStart(2, '0')).join('')
  }
}
